<template>
  <el-form
    class="rr-search-form"
    :class="{ dialog: type === 'dialog', 'rr-search-flex': mode == 'flex' }"
    ref="searchForm"
    :model="value"
    :inline="true"
    @submit.native.prevent="handleQuery"
  >
    <!-- flex-->
    <template v-if="mode == 'flex'">
      <div
        class="rr-form-item"
        :style="{
          height: isOpen ? 'auto' : formHeight + 'px',
          overflow: isOpen ? 'auto' : 'hidden',
        }"
      >
        <template v-for="(item, index) in json">
          <FormItem
            :key="index"
            :item="item"
            v-bind="item"
            :value="value[item.model]"
            @input="(val) => handleInput(item, val)"
          />
        </template>
        <div ref="expandFlag"></div>
      </div>
      <div class="rr-action">
        <el-button
          type="text"
          v-if="showOpen"
          @click="isOpen = !isOpen"
          size="medium"
          >{{ isOpen ? '' : ''
          }}<i :class="[isOpen ? 'el-icon-arrow-up' : 'el-icon-arrow-down']"></i
        ></el-button>
        <el-button type="primary" :size="$rocket.size" @click="handleQuery"
          ></el-button
        >
        <el-button :size="$rocket.size" @click="handleReset"></el-button>
      </div>
    </template>
    <!-- -->
    <template v-else-if="mode == 'inline'">
      <template v-for="(item, index) in json">
        <FormItem
          :key="index"
          :item="item"
          v-bind="item"
          :value="value[item.model]"
          @input="(val) => handleInput(item, val)"
        />
      </template>
      <el-form-item>
        <el-button type="primary" :size="$rocket.size" @click="handleQuery"
          ></el-button
        >
        <el-button :size="$rocket.size" @click="handleReset"></el-button>
      </el-form-item>
    </template>
  </el-form>
</template>
<script>
import FormItem from './../components/FormItem';
export default {
  name: 'SearchForm',
  props: {
    inline: String,
    type: String, // dialogQueryFormdialogQueryForm
    json: Array, // JSON
    model: Object, // v-model
  },
  components: { FormItem },
  data() {
    return {
      value: { ...this.model }, // 
      isOpen: false, // 
      formHeight: 0,
    };
  },
  computed: {
    // inlineflex
    mode() {
      return this.inline || this.$rocket.searchForm.inline;
    },
  },
  created() {
    this.setFastProp();
  },
  mounted() {
    // SearchForm
    if (this.$rocket.size == '') {
      this.formHeight = 62;
    } else if (this.$rocket.size === 'medium') {
      this.formHeight = 58;
    } else if (this.$rocket.size === 'small') {
      this.formHeight = 51;
    } else if (this.$rocket.size === 'mini') {
      this.formHeight = 47;
    }
    // flex
    if (this.mode == 'flex') this.calcHeight();
  },
  methods: {
    /**
     * 
     * @callback(val,values,model)//model
     */
    handleInput(item, val) {
      const { action } = item;
      /**
       * type: 'reset' 
       * model: 'all' 
       * model: ['state'] 
       * change
       */
      if (action && action.type === 'reset') {
        const modelList = action.model;
        // 
        if (modelList === 'all') {
          this.handleReset();
          this.value[item.model] = val;
        } else if (modelList) {
          // 
          modelList.map((key) => {
            if (Array.isArray(this.value[key])) {
              this.value[key] = [];
            } else {
              this.value[key] = undefined;
            }
            return key;
          });
        }
      }
      if (typeof item.change === 'function') {
        item.change(val, this.value, item.model, this.json);
      }
      // 
      if (
        ['daterange', 'monthrange', 'datetimerange'].includes(item.type) &&
        item.export
      ) {
        if (!Array.isArray(item.export)) {
          throw Error('item.export must be a Array');
        }
        this.value = {
          ...this.value,
          [item.model]: val || '',
          [item.export[0] || 'startTime']: val ? val[0] : '',
          [item.export[1] || 'endTime']: val ? val[1] : '',
        };
      } else {
        this.value = { ...this.value, [item.model]: val };
      }
    },
    /**
     * 
     * $refsAPI
     */
    handleReset() {
      this.$refs.searchForm.resetFields();
      this.json.map((item) => {
        if (
          ['daterange', 'monthrange', 'datetimerange'].includes(item.type) &&
          item.export
        ) {
          let dateRange = this.value[item.model];
          if (dateRange) {
            this.value[item.export[0]] = dateRange[0];
            this.value[item.export[1]] = dateRange[1];
          } else {
            delete this.value[item.export[0]];
            delete this.value[item.export[1]];
          }
        }
      });
      this.$emit('update:model', { ...this.value });
      this.$emit('handleReset', 1);
    },
    /**
     * 
     */
    handleQuery() {
      this.$emit('update:model', { ...this.value });
      this.$emit('handleQuery', 1);
    },
    setFastProp() {
      this.json.forEach((item) => {
        const fastProp = `$${item.model}`;
        if (item.model && !this.json[fastProp]) {
          Object.defineProperty(this.json, fastProp, {
            get() {
              return item;
            },
          });
        }
      });
    },
    // flex 
    calcHeight() {
      this.showOpen = false;
      const offsetTop = this.$refs.expandFlag.offsetTop;
      if (offsetTop > this.formHeight) this.showOpen = true;
    },
  },
  /**
   * 
   */
  unmounted() {
    window.onresize = null;
  },
  /**
   * model
   */
  watch: {
    model: {
      handler: function (val) {
        this.value = { ...val };
      },
      deep: true,
    },
  },
};
</script>
<style lang="scss" scoped>
// 
.rr-search-form {
  background-color: #fff;
  padding: 20px 20px 0;
  border-radius: 4px;
  border: 1px solid #ebeef5;
  &.rr-search-flex {
    display: flex;
    justify-content: space-between;
    .rr-form-item {
      position: relative;
      flex: 1;
    }
    .rr-action {
      min-width: 200px;
      text-align: right;
    }
  }
  .text-right {
    text-align: right;
  }
  &.dialog {
    background-color: #f7f8fa;
    border: none;
  }
  .hidden {
    display: none;
  }
  .el-row--flex {
    flex-wrap: wrap;
    .el-form-item {
      display: flex;
      .el-form-item__content {
        flex: 1;
        .el-date-editor {
          width: 100%;
        }
      }
    }
  }
}
</style>
